import { Component, OnInit } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, RequiredValidator, ValidationErrors, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from 'src/app/services/alert.service';
import { AuthService } from 'src/app/services/auth/auth.service';

@Component({
  selector: 'app-register-user',
  templateUrl: './register-user.component.html',
  styleUrls: ['./register-user.component.css']
})
export class RegisterUserComponent implements OnInit {

  constructor(private _fb:FormBuilder,
    private activeRoute: ActivatedRoute,
    private router:Router,
    private _notification:AlertService,
    private _auth:AuthService) {
      this.generateForm();
     }
  form:FormGroup;
  id:string;
  params:any;

  ngOnInit(): void {
 
    this.params=this.activeRoute.snapshot.queryParams;
    if(this.params && this.params.userid && this.params.userid!=='')
      this.id=this.params.userid;
    else
      this.router.navigate(['/auth/login']);
  }
  generateForm()
  {
    this.form=this._fb.group({
      id:[null],
      password:[null,Validators.required,Validators.minLength(8)],
      reTypePassword:[null,Validators.minLength(8)]
    })
  }
  
  public  matchValues(
    matchTo: string // name of the control to match to
  ): (AbstractControl) => ValidationErrors | null {
    return (control: AbstractControl): ValidationErrors | null => {
      return !!control.parent &&
        !!control.parent.value &&
        control.value === control.parent.controls[matchTo].value
        ? null
        : { isMatching: false };
    };
}

 onSubmit()
 {

   var data = {
     id:this.id,
     password:this.form.controls.password.value
   };

   this._auth.OnBoardingProcess(data).subscribe(res=>{
     if(res.isSuccessfull)
      {
        this._notification.confirm("OnBoarding Completed");
        this.router.navigate(['/auth/login']);
      }
      else{
    
        this._notification.error(res.message);
      }
   })
 }
}
