import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AlertService } from 'src/app/services/alert.service';
import { AuthService } from 'src/app/services/auth/auth.service';

@Component({
  selector: 'app-new-password',
  templateUrl: './new-password.component.html',
  styleUrls: ['./new-password.component.css']
})
export class NewPasswordComponent implements OnInit {

 constructor(private _fb:FormBuilder,
    private activeRoute: ActivatedRoute,
    private router:Router,
    private _notification:AlertService,
    private _auth:AuthService) {
      this.generateForm();
     }
  form:FormGroup;
  id:string;
  params:any;

  ngOnInit(): void {
 
    this.params=this.activeRoute.snapshot.queryParams;
    if(this.params && this.params.userid && this.params.userid!=='')
      this.id=this.params.userid;
    else
      this.router.navigate(['/auth/login']);
  }
  generateForm()
  {
    this.form=this._fb.group({
      id:[null],
      password:[null,Validators.required,Validators.minLength(8)],
      reTypePassword:[null,Validators.minLength(8)]
    })
  }

}
